import React from "react"
import Layout from "../components/layout"

export default ({data}) => {
  console.log(data);
  const dataPage = data.coursesYaml;
  return (
    <Layout>
      <div>
        Hello course
        <p>Info: {dataPage.info}</p>
      </div>
    </Layout>
  )
}

export const query = graphql`
  query ($slug: String){
    coursesYaml (basic_info: {slug: { eq:$slug }}){
      basic_info{
        slug
      }
      info
    }
  }
`;